.blackBack {
  background: #111;
}

a {
  text-decoration: none;
}

.App {
  animation: blockApear 0.8s ease forwards 5s;
}

@keyframes blockApear {
  from {
    display: none;
    opacity: 0.1;
  }

  to {
    display: block;
    opacity: 1;
  }
}